@import "normalize.css";
@import "./variables";
@import "./calculated";

html {
  font-size: $font-size-baseline;
  font-family: $font-family;
}

* {
  transition: .3s;
}

body {
  background-image: url("../img/creampaper_bw.png");
  background-repeat: repeat;
  font-size: 16px;
  // padding-top: 
}

.hdr {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  // justify-content: center;
  padding: 0 2rem;
  margin: 0 auto;
  padding: 0;
  width: 100%;
  max-width: 1280px;
}

