.nv {
  padding: 0 2rem;
  // font-size: 1.1rem;
  font-size: 1rem;
  // border-bottom: 2px solid mix(complement($color-primary), $color-tint, 25%);
  border-bottom: 1px solid mix($color-primary, $color-shade, 80%);
  margin-bottom: .75rem;
  padding: 0;
  flex: 1;
}

.nv ul {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 0;
  margin: .5rem;
}

.nv li {
  list-style: none;
  margin: 1rem;
}

.brnd, .nv li a {
  text-decoration: none;
  font-weight: 600;
  border-radius: 4px;
  padding: .6rem 1rem;
  color: mix($color-primary, $color-shade, 100%);
  text-align: center;
}

.brnd {
  font-size: 1.2rem;
  margin-right: 3rem;
  font-weight: bold;
  border: 3px solid;
  border-radius: 8px;
  margin: .75rem 6rem .75rem 2rem;
  align-self: flex-start;
}

.brnd:hover {
  background-color: mix($color-primary, $color-shade, 90%);
  background-color: mix($color-primary, $color-tint, 80%);
  color: #FFF;
  box-shadow: 0 0 1rem inset rgba(0,0,0,.2);
}

.nv li a:hover {
  background-color: mix($color-primary, $color-tint, 10%);
}

section {
  min-height: 80vh;
  transition: background-color 1s, box-shadow .2s;
  // background-color: rgba(mix($color-primary, $color-tint, 5%), 1);

  // border: 1px solid #666;
  // box-shadow: 0 2px .5rem rgba(0,0,0,.15);
  // background-color: rgba(255,255,255,.5);
}

section:hover {
  z-index: 300;
  // box-shadow: 0 0 1rem rgba(0,0,0,.2);
  background-color: rgba(255,255,255,.75);
  box-shadow: 0 0 .25rem rgba(mix($color-primary, $color-shade, 25%), .1);
}

.dt,.crcl,.sqr,.rndSqr {
  list-style-type: none;
}

.dt li,.crcl li,
.sqr li,.rndSqr li {
  position: relative;
}

.sqr li::before {
  border: 0.18em solid $color-primary;
}

.sqr li::before {
  border: 0.18em solid $color-primary;
  background-color: $color-primary;
}

.rndSqr li::before {
  border-radius: .25em;
  border: 0.18em solid $color-primary;
}

.crcl li::before {
  border-radius: 1em;
  border: 0.18em solid $color-primary;
}

.dt li::before {
  border-radius: 1em;
  border: 0.15em solid $color-primary;
  background-color: $color-primary;
}

.dt li::before, .crcl li::before,
.sqr li::before, .rndSqr li::before {
  content: "";
  position: absolute;
  left: -1.15em;
  top: 0.15em;
  width: 0.4em;
  height: 0.4em;
}


.clrNm {
  list-style: none;
  counter-reset: li;
}

.clrNm li {
  counter-increment: li;
}


.clrNm li::before {
  content: counter(li);
  color: $color-primary;
  display: inline-block;
  width: 1em;
  margin-left: -1em
}


.flx-rw {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.flx-cl {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flx-rw-rv {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
}

.flx-cl-rv {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
}






.tbl {
  margin: 1rem;
}

.tbl th, .tbl td {
  padding: 1rem 2rem;
} 

.tbl th {
  border-bottom: 1px solid mix($color-primary, $color-shade, 90%);
  border-left: 1px solid mix($color-primary, $color-shade, 90%);
  font-weight: bolder;
  background-color: rgba(0,0,0,.015);
}

.tbl td {
  border-bottom: 1px solid mix($color-primary, $color-tone, 20%);
  border-left: 1px solid mix($color-primary, $color-tone, 20%);
}

.tbl td:first-child, .tbl th:first-child  {
  border-left: none;
}

.btn-outline {
  font-size: 1.25rem;
  padding: .8rem 1.2rem;
  margin: 1rem;
  border-radius: 3px;
  font-size: 18px;
  font-weight: 700;
  color: $color-primary;
  border: 2px solid $color-primary;
  text-decoration: none;
  position: relative;
  background-color: transparent;
}

.btn-flat {
  font-size: 1.25rem;
  padding: .8rem 1.2rem;
  margin: 1rem;
  border-radius: 3px;
  font-size: 18px;
  font-weight: 700;
  border: none;
  // background-color: #3c68cf;
  color:#fff;
  background-color: $color-primary;
  text-decoration: none;
  position: relative;
}

.btn-grad {
  font-size: 1.25rem;
  padding: .8rem 1.2rem;
  margin: 1rem;
  border-radius: 3px;
  font-size: 18px;
  font-weight: 700;
  border: none;
  // background-color: #3c68cf;
  color:#fff;
  // background-color: mix($color-primary, $color-shade, 100%);
  text-decoration: none;

  position: relative;
  background-image: linear-gradient(
    .25turn,
    mix($color-primary, $color-secondary, 0%),
    mix($color-primary, $color-secondary, 60%),
    mix($color-primary, $color-secondary, 100%)
  );
  z-index: 1;
  border-radius: 3px;
  transition: .5s;
  text-shadow: 0 2px 2px mix($color-primary, $color-shade, 50%);

  &:hover {
    text-shadow: 0 .5px 1px mix($color-primary, $color-shade, 90%);
  }

}

.btn-grad::before {
  position: absolute;
  border-radius: 3px;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  transition: opacity 0.6s linear;
  opacity: 0;
  background-image: linear-gradient(
    2turn,
    mix($color-primary, $color-secondary, 0%),
    mix($color-primary, $color-secondary, 60%),
    mix($color-primary, $color-secondary, 100%)
  );
}

.btn-grad:hover::before {
  opacity: 1;
}


.btn-shadow {
  box-shadow: 0px .3rem .8rem rgba(mix($color-primary, $color-shade, 70%), .5);
  box-shadow: 0.5rem 1rem 2rem rgba(175,76,23, .4);
}

.btn-shadow:hover {
  box-shadow: 0px .1rem .2rem rgba(mix($color-primary, $color-shade, 70%), .5);
  box-shadow: -.5rem 0.5rem 1rem rgba(175,76,23,.5);
}






























