/*****   Mobile   ****/
@media only screen and (max-width: 600px) {
  .hdr {
    flex-direction: column;
    align-items: center;
    font-size: 3rem;
    width: 100%;
    padding: 0;
    margin: 0;
  }

  .nv, nv {
    width: 100%;
    padding: 0;
    margin: 0;
  }

  .nv ul {
    flex-direction: column;
    align-items: center;
    margin: 0;
    padding: 0;
    width: 100%;
  }

  .nv li {
    width: 100%;
    padding: 0;
  }

  .nv li a {
    border-radius: 0;
    width: 100%;
    display: block;
    border-bottom: 1px solid mix($color-primary, $color-shade, 100%);
  }

  // section, .section {
  //   width: 100%;
  //   min-height: 100vh;
  // }

  .content {
    padding: 2rem;
    flex-flow: column;
    justify-content: center;
  }
  
  .content-text {
    padding-right: 0;
    font-size: 1rem;
    text-align: center;
  }

  .content-text h1 {
    font-size: 2.4rem;
  }

  .content-text p {
    /* margin-top: 2rem; */
    margin-right: 0;
  }
  button, .button {
    margin: 2rem auto;
    display: block;
  }
}








