.gradient {
  position: relative;
  // background: radial-gradient(
  //   #007bff,
  //   #cde5ff,
  //   #4da3ff
  // );
  background-image: linear-gradient(
    to right bottom,
    #cde5ff,
    #4da3ff,
    #4da3ff,
    #007bff
  );
  z-index: 1;
}

.gradient::before {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 4px;
  background-image: linear-gradient(
    to left bottom,
    #cde5ff,
    #4da3ff,
    #4da3ff,
    #007bff
  );
  z-index: -1;
  transition: opacity 0.6s linear;
  opacity: 0;
}

.gradient:hover::before {
  opacity: 1;
}